import { useEffect } from "react"
import { DSMock, DSMclaimer } from "components";


const DSM_PAGE = () => {
  useEffect(() => {
    document.title = "SetaDiagnosticAndStatisticalManualOfMentalDisorders"
  })

  return (
    <center>
    <section className="card">

    <DSMock name="Childhood-Onset Fluency Disorder (Stuttering)" code="F80.81">
      <ol type="A">
        <li>
          Disturbances in the normal fluency and time patterning of speech that are
          inappropriate for the individual's age and language skills, persist over time, and
          are characterized by frequent and marked occurrences of one (or more) of the
          following:
          <ol type="1">
            <li>Sound and syllable repetitions.</li>
            <li>Sound prolongations of consonants as well as vowels.</li>
            <li>Broken words (e.g., pauses within a word).</li>
            <li>Audible or silent blocking (filled or unfilled pauses in speech).</li>
            <li>Circumlocutions (word substitutions to avoid problematic words).</li>
            <li>Words produced with an excess of physical tension.</li>
            <li>Monosyllabic whole-word repetitions (e.g., I-I-I-I see him).</li>
          </ol>
        </li>
        <li>
          The disturbance causes anxiety about speaking or limitations in effective
          communication, social participation, or academic or occupational performance,
          individually or in any combination.
        </li>        
        <li>
          The onset of symptoms is in the early developmental period. (Note: Later-onset
          cases are diagnosed as F98.5 adult-onset fluency disorder.)
        </li>
        <li>
          The disturbance is not attributable to a speech-motor or sensory deficit,
          dysfluency associated with neurological insult (e.g., stroke, tumor, trauma), or
          another medical condition and is not better explained by another mental
          disorder.
        </li>
      </ol>

    </DSMock>

    <DSMclaimer></DSMclaimer>

    </section>
    </center>
  )
};

export default DSM_PAGE;