import { useEffect } from "react"
import { DSMock, DSMode, DSMclaimer } from "components";


const DSM_PAGE = () => {
  useEffect(() => {
    document.title = "SetaDiagnosticAndStatisticalManualOfMentalDisorders"
  })

  return (
    <center>
    <section className="card">

    <DSMock name="Stereotypic Movement Disorder" code="F98.4">
      <ol type="A">
      <li>
        Repetitive, seemingly driven, and apparently purposeless motor behavior (e.g.,
        hand shaking or waving, body rocking, head banging, self-biting, hitting own
        body).
      </li>
      <li>
        The repetitive motor behavior interferes with social, academic, or other activities
        and may result in self-injury.
      </li>
      <li>
        Onset is in the early developmental period.
      </li>
      <li>
        The repetitive motor behavior is not attributable to the physiological effects of a
        substance or neurological condition and is not better explained by another
        neurodevelopmental or mental disorder (e.g., trichotillomania [hair-pulling
        disorder], obsessive-compulsive disorder).
      </li>                
      </ol>
    </DSMock>

    <DSMode>
      <i>Specify</i> if:
      <ul>
        <li><b>With self-injurious behavior</b> (or behavior that would result in an injury if preventive measures were not used)</li>
        <li><b>Without self-injurious behavior</b></li>
      </ul>
      <i>Specify</i> if:
      <ul>
        <li>
          <b>Associated with a known genetic or other medical condition,
          neurodevelopmental disorder, or environmental factor</b> (e.g., Lesch-Nyhan
          syndrome, intellectual developmental disorder [intellectual disability], intrauterine
          alcohol exposure)
        </li>

        <b>Coding note:</b> Use additional code to identify the associated genetic or other
        medical condition, neurodevelopmental disorder, or environmental factor.
        </ul>
        <i>Specify</i> current severity:
        <ul>
          <li><b>Mild:</b> Symptoms are easily suppressed by sensory stimulus or distraction.</li>
          <li><b>Moderate:</b> Symptoms require explicit protective measures and behavioral
          modification.</li>
          <li><b>Severe:</b> Continuous monitoring and protective measures are required to prevent
          serious injury.</li>          
        </ul>
    </DSMode>

    <DSMclaimer></DSMclaimer>

    </section>
    </center>
  )
};

export default DSM_PAGE;