import { useEffect } from "react"
import { DSMock, DSMclaimer } from "components";


const DSM_PAGE = () => {
  useEffect(() => {
    document.title = "SetaDiagnosticAndStatisticalManualOfMentalDisorders"
  })

  return (
    <center>
    <section className="card">

    <DSMock name="Developmental Coordination Disorder" code="F82">
      <ol type="A">
        <li>
          The acquisition and execution of coordinated motor skills is substantially below
          that expected given the individual's chronological age and opportunity for skill
          learning and use. Difficulties are manifested as clumsiness (e.g., dropping or
          bumping into objects) as well as slowness and inaccuracy of performance of motor skills (e.g.,
          catching an object, using scissors or cutlery, handwriting, riding a bike, or
          participating in sports).
        </li>
        <li>
          The motor skills deficit in Criterion A significantly and persistently interferes with
          activities of daily living appropriate to chronological age (e.g., self-care and selfmaintenance) and impacts academic/school productivity, prevocational and
          vocational activities, leisure, and play.
        </li>
        <li>
          Onset of symptoms is in the early developmental period.
        </li> 
        <li>
          The motor skills deficits are not better explained by intellectual developmental
          disorder (intellectual disability) or visual impairment and are not attributable to a
          neurological condition affecting movement (e.g., cerebral palsy, muscular
          dystrophy, degenerative disorder).
        </li>                              
      </ol>

    </DSMock>

    <DSMclaimer></DSMclaimer>

    </section>
    </center>
  )
};

export default DSM_PAGE;