import { useEffect } from "react"
import { DSMock, DSMclaimer } from "components";


const DSM_PAGE = () => {
  useEffect(() => {
    document.title = "SetaDiagnosticAndStatisticalManualOfMentalDisorders"
  })

  return (
    <center>
    <section className="card">

    <DSMock name="Language Disorder" code="F80.2">
      <ol type="A">
        <li>
          Persistent difficulties in the acquisition and use of language across modalities
          (i.e., spoken, written, sign language, or other) due to deficits in comprehension
          or production that include the following:
          <ol type="1">
            <li>Reduced vocabulary (word knowledge and use).</li>
            <li>Limited sentence structure (ability to put words and word endings together to form sentences based on the rules of grammar and morphology).</li>
            <li>Impairments in discourse (ability to use vocabulary and connect sentences to explain or describe a topic or series of events or have a conversation).</li>
          </ol>
        </li>
        <li>
          Language abilities are substantially and quantifiably below those expected for
          age, resulting in functional limitations in effective communication, social
          participation, academic achievement, or occupational performance, individually
          or in any combination.
        </li>
        <li>
          Onset of symptoms is in the early developmental period.
        </li>
        <li>
          The difficulties are not attributable to hearing or other sensory impairment, motor
          dysfunction, or another medical or neurological condition and are not better
          explained by intellectual developmental disorder (intellectual disability) or global
          developmental delay
        </li>
      </ol>
    </DSMock>

    <DSMclaimer></DSMclaimer>

    </section>
    </center>
  )
};

export default DSM_PAGE;